import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import TextChild from './TextChild';
class SideBarControl extends Component{
	
	constructor(){
		super()
		this.state = {
			display: "Resume"
		}
	}
	openTab(evt, number){
		var i, tabcontent, tablinks;
		number = number.toString();
		if(number === "All"){
			tabcontent = document.getElementsByClassName("tabcontent");
			for(i = 0; i < tabcontent.length; i++){
				tabcontent[i].style.display = "block";
				
			}
			tablinks = document.getElementsByClassName("tablinks");
			for(i = 0; i < tablinks.length; i++){
				tablinks[i].className = tablinks[i].className.replace(" active","");
			}
			evt.currentTarget.className += " active";
			return;
		}
		tabcontent = document.getElementsByClassName("tabcontent");
		for(i = 0; i < tabcontent.length; i++){
			tabcontent[i].style.display = "none";
		}
		tablinks = document.getElementsByClassName("tablinks");
		for(i = 0; i < tablinks.length; i++){
			tablinks[i].className = tablinks[i].className.replace(" active","");
		}
		document.getElementById(number).style.display = "block";
		evt.currentTarget.className += " active";
	}
	changeState(newState){
		this.setState({
			display: newState
		})
	}
	render(){
		if(this.state.display == "Resume"){
			return(
				<div>
					<div className = "row">
						<div className = "col-md-12 showcaseinner">
							<div className = "row">
								<div className = "col-md-12">
									<TextChild text = "Hayden Roeder" content1 = "hroeder1@binghamton.edu" content2 = "Github"></TextChild>
								</div>
							</div>
							<div className = "row showcaseNav">
								
								<div className = "col-md-4">
									<button onClick = {() => this.changeState("Resume")} className = "btn btn-basic btn-block showcaseButton active"> Resume </button>
								</div>
								<div className = "col-md-4">
									<button onClick = {() => this.changeState("Coursework")} className = "btn btn-basic btn-block showcaseButton"> Coursework </button>	
								</div>
								<div className = "col-md-4">
									<button onClick = {() => this.changeState("Projects")} className = "btn btn-basic btn-block showcaseButton"> Projects </button>
								</div>
							</div>
						</div>
					</div>
					<div className = "row">
						<div className = "col-md-12 showcaseLower">
							<iframe className = "resumeView" src="https://drive.google.com/file/d/1n0VrsGvu1nvmxkHIslnOCAtslZBzvOpa/preview"></iframe>
						</div>
					</div>
				</div>
				
			)
		}
		if(this.state.display == "Projects"){
			return(
				<div>
					<div className = "row">
						<div className = "col-md-12 showcaseinner">
							<div className = "row">
								<div className = "col-md-12">
									<TextChild text = "Hayden Roeder" content1 = "hroeder1@binghamton.edu" content2 = "Github"></TextChild>
								</div>
							</div>
							<div className = "row showcaseNav">
								
								<div className = "col-md-4">
									<button onClick = {() => this.changeState("Resume")} className = "btn btn-basic btn-block showcaseButton"> Resume </button>
								</div>
								<div className = "col-md-4">
									<button onClick = {() => this.changeState("Coursework")} className = "btn btn-basic btn-block showcaseButton"> Coursework </button>	
								</div>
								<div className = "col-md-4">
									<button onClick = {() => this.changeState("Projects")} className = "btn btn-basic btn-block showcaseButton active"> Projects </button>
								</div>
							</div>
						</div>
					</div>
					<div className = "row">
						<div className = "col-md-3">
							<div className = "row">
								<button className = "tablinks btn btn-basic btn-block showcaseButton" onClick = {(e) => this.openTab(e, 'All')}>All</button>
								<button className = "tablinks btn btn-basic btn-block showcaseButton" onClick = {(e) => this.openTab(e, 'QBWS')}>Question Bank Webscraper</button>
								<button className = "tablinks btn btn-basic btn-block showcaseButton" onClick = {(e) => this.openTab(e, 'LLR')}>Lunar Lander Redesign</button>
								<button className = "tablinks btn btn-basic btn-block showcaseButton" onClick = {(e) => this.openTab(e, 'FK')}>HackBU 2019 Submission</button>
								<button className = "tablinks btn btn-basic btn-block showcaseButton" onClick = {(e) => this.openTab(e, 'PP')}>Poker Project</button>
								<button className = "tablinks btn btn-basic btn-block showcaseButton" onClick = {(e) => this.openTab(e, 'AC')}>Assembly Compiler</button>
								<button className = "tablinks btn btn-basic btn-block showcaseButton" onClick = {(e) => this.openTab(e, 'WA')}>Weather App</button>
							</div>
						</div>
						<div className = "col-md-9 showcaseLower textLeft">
							<div id = "QBWS" className = "tabcontent">
								<div className = "row">
									<h4>Question Bank Webscraper</h4>
									<ul style = {{listStyle: 'none'}}>
										<li><p>C# Visual Studio Application to collect online quiz data and develop a question bank to study from</p></li>
										<li><p>Learned methods of collecting data from the web as well as basic SQL database concepts</p></li>
										<li><p><a href = "https://github.com/HaydenRoeder1/GeographyQuizWebScraper">Github</a></p></li>
									</ul>
								</div>
								<div className = "row">
									<img src = "GWSCapture.PNG" style = {{width: "100%"}}/>
								</div>
								
							</div>
							<div id = "LLR" className = "tabcontent">
								<div className = "row">
									<h4>Lunar Lander Redesign</h4>
									<ul style = {{listStyle: 'none'}}>
										<li><p>Python game using the PyGame library</p></li>
										<li><p>Worked on a team of three as the backend programmer. Developed a physics and positioning system so the frontend programmer only had to worry about UI and drawing sprites at their position</p></li>
										<li><p>Also worked on various aspects of the game such as a highscore saver and input logic</p></li>
										<li><p><a href = "https://github.com/HaydenRoeder1/LunarLanderClone-CS110-">Github</a></p></li>
									</ul> 
								</div>
								<div className = "row">
									<img src = "LLRCapture.png" style = {{width: "100%"}}/>
								</div>
							</div>
							<div id = "FK" className = "tabcontent">
								<div className = "row">
									<h4>Fork-Knife</h4>
									<ul style = {{listStyle: 'none'}}>
										<li><p>C++ linux terminal game</p></li>
										<li><p>Text-based parody of Fortnite for submission into the "Funniest Hack" category</p></li>
										<li><p>Worked on a team of three as the main programmer. Developed a class structure for the weapons and items in the game, as well as a prompt system for the combat in the game</p></li>
										<li><p><a href = "https://github.com/HaydenRoeder1/Fork-Knife">Github</a></p></li>
									</ul> 
								</div>
								<div className = "row">
									<img src = "FKCapture.PNG" style = {{width: "100%"}}/>
								</div>
								
							</div>
							
							<div id = "AC" className = "tabcontent">
								<div className = "row">
									<h4>Assembly Compiler</h4>
									<ul style = {{listStyle: 'none'}}>
										<li><p>Eclipse Java Project</p></li>
										<li><p>Compiles and runs basic assembly code and views the memory as it runs</p></li>
										<li><p>Worked on a team of two as the backend programmer.  Developed the systems that ran the given code</p></li>
										<li><p><a href = "https://github.com/HaydenRoeder1/CS140-Data-Structures-and-Algorithms-Java-/tree/master">Github</a></p></li>
									</ul>
								</div>
								<div className = "row">
									<img src = "ACCapture.PNG" style = {{width: "100%"}}/>
								</div>
							</div>
							
							<div id = "WA" className = "tabcontent">
								<div className = "row">
									<h4>Weather App</h4>
									<ul style = {{listStyle: 'none'}}>
										<li><p>Web Application made with React</p></li>
										<li><p>Project to help me learn concepts of web development as well as working with APIs</p></li>
										<li><p>Learned how to work with JSON files as well as basic concepts of React</p></li>
										<li><p><Link to="/weather_app/">Live Demo</Link></p></li>
									</ul> 
								</div>
							</div>
							<div id = "PP" className = "tabcontent">
								<div className = "row">
									<h4>Poker Project</h4>
									<ul style = {{listStyle: 'none'}}>
										<li><p>Linux terminal program written in C</p></li>
										<li><p>Reads in a file of several poker games determines the winner of each game based on each player's available hand</p></li>
										<li><p>Worked on a team of two and focused on input and output of the program</p></li>
										<li><p>Created a parser that reads in hands into arrays for easier analysis later on to determine the best available hand</p></li>
										<li><p><a href = "https://github.com/HaydenRoeder1/CS220-Introduction-to-Computer-Systems--C-and-Assembly-">Github</a></p></li>
									</ul> 
								</div>
							</div>
								
							
						</div>
					</div>
				</div>
			
			)
		}
		if(this.state.display == "Coursework"){
			return(
				<div>
					<div className = "row">
						<div className = "col-md-12 showcaseinner">
							<div className = "row">
								<div className = "col-md-12">
									<TextChild text = "Hayden Roeder" content1 = "hroeder1@binghamton.edu" content2 = "Github"></TextChild>
								</div>
							</div>
							<div className = "row showcaseNav">
								
								<div className = "col-md-4">
									<button onClick = {() => this.changeState("Resume")} className = "btn btn-basic btn-block showcaseButton"> Resume </button>
								</div>
								<div className = "col-md-4">
									<button onClick = {() => this.changeState("Coursework")} className = "btn btn-basic btn-block showcaseButton  active"> Coursework </button>	
								</div>
								<div className = "col-md-4">
									<button onClick = {() => this.changeState("Projects")} className = "btn btn-basic btn-block showcaseButton"> Projects </button>
								</div>
							</div>
						</div>
					</div>
					<div className = "row">
						<div className = "col-md-2">
							<div className = "row">
								<button className = "tablinks btn btn-basic btn-block showcaseButton" onClick = {(e) => this.openTab(e, "All")}>All</button>
								<button className = "tablinks btn btn-basic btn-block showcaseButton" onClick = {(e) => this.openTab(e, 110)}>CS: 110</button>
								<button className = "tablinks btn btn-basic btn-block showcaseButton" onClick = {(e) => this.openTab(e, 120)}>CS: 120</button>
								<button className = "tablinks btn btn-basic btn-block showcaseButton" onClick = {(e) => this.openTab(e, 140)}>CS: 140</button>
								<button className = "tablinks btn btn-basic btn-block showcaseButton" onClick = {(e) => this.openTab(e, 220)}>CS: 220</button>
								<button className = "tablinks btn btn-basic btn-block showcaseButton" onClick = {(e) => this.openTab(e, 240)}>CS: 240</button>
								<button className = "tablinks btn btn-basic btn-block showcaseButton" onClick = {(e) => this.openTab(e, 380)}>PLSC: 380j</button>
							</div>
						</div>
						<div className = "col-md-10 showcaseLower textLeft">
								<div id = "380" className = "tabcontent">
									<h4>PLSC 380j: Analyzing Politics Using R</h4>
									<ul style = {{listStyle: 'none'}}>
										<li><p>Political science class with a data analytics focus</p></li>
										<li><p>Learned general concepts of R Studio</p></li>
										<li><p>Learned methods of analyzing datasheets to draw relavent conclusions</p></li>
										
									</ul> 
								</div>
								<div id = "240" className = "tabcontent">
									<h4>CS 240: Data Structures and Algorithms</h4>
									<ul style = {{listStyle: 'none'}}>
										<li><p>C++ based class to introduce the concepts of how to choose the proper algorithm or data structure for a given task</p></li>
										<li><p>Worked on multiple projects of solar system simulation designed to expose students to various data structure applications</p></li>
										<li><p>Gained more advanced experience in the language of C++</p></li>
										
									</ul> 
								</div>
								<div id = "220" className = "tabcontent">
									<h4>CS 220: Architecture from a Programming Perspective</h4>
									<ul style = {{listStyle: 'none'}}>
										<li><p>More advanced machine architecture topics expanding from CS 120</p></li>
										<li><p>Worked more in depth in C as well as x86 assembly</p></li>
										<li><p>Created a C program to determine the winner of a game of poker</p></li>
										<li><p>Used GDB to analyze a compiled C program to diffuse a "bomb"</p></li>
										
									</ul> 
								</div>
								<div id = "140" className = "tabcontent">
									<h4>CS 140: Programming with Objects</h4>
									<ul style = {{listStyle: 'none'}}>
										<li><p>Continuation of object oriented concepts from CS110</p></li>
										<li><p>Learned more advanced concepts of object oriented programming in Java</p></li>
										<li><p>Worked as the backend programmer for a simple <a href = "https://github.com/HaydenRoeder1/CS140-Data-Structures-and-Algorithms-Java-/tree/master/project"> Assembly Compiler</a></p></li>
									</ul> 
								</div>
								<div id = "120" className = "tabcontent">
									<h4>CS 120: Computer Systems I: Machine Organization</h4>
									<ul style = {{listStyle: 'none'}}>
										<li><p>Introduction to machine architecture</p></li>
										<li><p>Learned basic circuit design for processors</p></li>
										<li><p>Learned basic MIPS Assembly as well as some C programming</p></li>
									</ul>
								</div>
								<div id = "110" className = "tabcontent">
									<h4>CS 110: Programming Concepts and Applications</h4>
									<ul style = {{listStyle: 'none'}}>
										<li><p>Introduction to programming through python.</p></li>
										<li><p>Learned to program in a linux environment</p></li>
										<li><p>Worked as the backend lead for a lunar lander style game on a team of 3</p></li>
									</ul>
								</div>
								
							
						</div>
					</div>
				</div>
			)
		}
	}
}

export default SideBarControl;