import React, { Component } from 'react'
class NavigationBar extends Component{

	constructor(){
		super()
		this.state = {
			display: "Collapse"
		}
	}
	changeState(newState){
		this.setState({
			display: newState
		})
	}
	render(){
		if(this.state.display == "Open" || this.state.display == "Collapse"){
			return(
				<div className = "row navigation">
		
					<button className = "btn btn-basic leftButton"> Home </button>
				
					<button className = "btn btn-basic leftButton"> About Me </button>
				
					<button className = "btn btn-basic leftButton"> Projects </button>
						
				</div>
			)
		}
		if(this.state.display == "Collapse"){
			return(
				<div className = "row navigation">
					<ul className = "navBar">
						<li>
							<button onClick = {() => this.changeState("Open")} className = "btn btn-basic leftButton"> &gt; </button>
						</li>
					</ul>
				</div>
			)
		}
	}
}

export default NavigationBar;