import React from 'react';
import {Map, InfoWindow, Marker, GoogleApiWrapper} from 'google-maps-react';
const GMAPS_KEY = "AIzaSyDWZwv9gRbdAZDUNZTP7aLkzGznybyppws";
export class MapContainer extends React.Component{
	state = {
		markers:
			{
				position: {lat: 0, lng: 0},
				defaultAnimation: 2,
				key: Date.now()
			},
			mapCenter: {lat: 0, lng: 0}
	}
	mapClicked = async(mapProps, map, clickEvent) => {
		this.setState({markers:
			{
				position: clickEvent.latLng,
				defaultAnimation: 2,
				key: Date.now()
			},
			mapCenter: map.center
		});
		const lat = this.state.markers.position.lat();
		const lng = this.state.markers.position.lng();

		const api_call = await fetch(`https://maps.googleapis.com/maps/api/geocode/json?latlng=${lat},${lng}&key=${GMAPS_KEY}`);
		const data = await api_call.json();
		if(data.results[0] == undefined){
			return;
		}
		const address = data.results[0].formatted_address;
		var country = address.substring(address.lastIndexOf(',') + 1);
		var city = address.substring(address.indexOf(',') + 1);
		city = city.substring(0, city.indexOf(','));
		if(country.includes("USA")){
			country = "US";
		}
		this.props.setLocation(city, country);
	}

	render(){
		return(
			<Map google = {this.props.google} zoom = {5} center = {this.state.mapCenter} onClick = {this.mapClicked}>
				<Marker 
					onClick = {this.onMarkerClick} 
					name = {'Current_location'}
					position = {this.state.markers.position}
					
				/>
				<InfoWindow onClose = {this.onInfoWindowClose}>
				<div>
				</div>
				</InfoWindow>
			</Map>
		);
	}
}
export default GoogleApiWrapper({
	apiKey: "AIzaSyDWZwv9gRbdAZDUNZTP7aLkzGznybyppws"
})(MapContainer)