import React, { Component } from 'react';

import './WeatherApp.css';
import Titles from "./components/Titles";
import Form from "./components/Form";
import Weather from "./components/Weather";
import MapContainer from "./components/MapContainer";
const API_KEY = "7b2cbff8fb0099b9696399b7a954bd03";

const backgrounds = [
	"newBG1.jpeg",
	"newBG2.jpeg",
	"newBG3.jpeg",
	"newBG4.jpeg",
	"newBG5.jpeg",
	"newBG6.jpeg",
	"newBG7.jpeg",
	"newBG8.jpeg"
]
class WeatherApp extends Component {
	
	constructor(){
		super();
		this.index = Math.floor(Math.random() * backgrounds.length - 1);
		if(this.index < 0){this.index = 0;}
		if(this.index >= backgrounds.length){this.index = backgrounds.length-1;}
		this.backgroundName = backgrounds[this.index]; 
		this.randStyle = {
			background: `url(${this.backgroundName})`,
			backgroundPosition: "center",
			backgroundRepeat: "no-repeat",
			backgroundSize: "cover",
			height: "100vh",
			display: "flex",
			justifyContent: "center",
			alignItems: "center"
		};
	}
	state = {
		temperature: undefined,
		latitude: undefined,
		longitude: undefined,
		city: undefined,
		country: undefined,
		humidity: undefined,
		description: undefined,
		error: undefined
		
	};
	getWeather = async(e) => {
		e.preventDefault();
		const city = e.target.elements.city.value;
		const country = e.target.elements.country.value;
		if(!city || !country){
			this.setState({
				temperature: undefined,	
				latitude: undefined,
				longitude: undefined,
				city: undefined,
				country: undefined,
				humidity: undefined,
				description: undefined,
				error: "Please enter city and country values"
			})
		}else{
			const api_call = await fetch(`http://api.openweathermap.org/data/2.5/weather?q=${city},${country}&appid=${API_KEY}&units=imperial`).catch(
				this.setState({
					temperature: undefined,
					latitude: undefined,
					longitude: undefined,
					city: undefined,
					country: undefined,
					humidity: undefined,
					description: undefined,
					error: "Could not find data for this location"
				})
			); 
			const data = await api_call.json();
			
			if(data.cod != "404"){
				if(city && country){
					
					this.setState({
						temperature: data.main.temp,
						latitude: data.coord.lat,
						longitude: data.coord.lon,
						city: data.name,
						country: data.sys.country,
						humidity: data.main.humidity,
						description: data.weather[0].description,
						error: ""
						
					});
				}
			}
		}
	}
	setLocation = (city, country) => {
			document.getElementById("city").value = city;
			document.getElementById("country").value = country;
		}
	render() {
		  
		return (
			<div>
				<div style = {this.randStyle}>
					<div className = "main">
						<div className = "container">
							<div className = "innerContainer">
								<div className = "row title-container">
									<div className = "col-md-12">
										<Titles />
									</div>
								</div>
								<div className = "row padding inputForm">
									<div className = "col-md-12">
										<Form getWeather = {this.getWeather}/>
									</div>
									
								</div>
								<div className = "row">
									<div className = "col-md-6">
										<div className = "row">
											<MapContainer setLocation = {this.setLocation} markerLat = {this.state.latitude} markerLng = {this.state.longitude}/>
										</div>
									</div>
									<div className = "col-md-1"/>
									<div className = "col-md-5">
										
										<Weather
											temperature={this.state.temperature}
											city={this.state.city}
											country = {this.state.country}
											humidity = {this.state.humidity}
											description = {this.state.description}
											error = {this.state.error}
										/>
									</div>
									
								</div>	
								
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	 }
}
export default WeatherApp;
